@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
/* Movie List */
.container {max-width:1200px; margin:0 auto; padding:15px;}
.container .movie_info {overflow:hidden; margin-bottom:15px; padding:15px; background:#fff; box-shadow:2px 2px 3px 1px rgb(0 0 0 / 10%);}
.container .movie_info img {float:left; width:115px;}
.container .movie_data {overflow:hidden; padding-left:15px;}
.container .movie_data .title {font-size:24px; font-weight:700; color:lightcoral;}
.container .movie_data .title .year {display:inline-block; margin-left:.25em; font-size:.75em;}
.container .movie_data .genres {margin-top:.5em;}
.container .movie_data .genres li {display:inline-block; font-size:12px; color:#666;}
.container .movie_data .genres li:after {content:'|'; padding:0 .25em;}
.container .movie_data .genres li:last-child:after {display:none;}
.container .movie_data .summary {margin-top:1em; padding-top:15px; border-top:1px solid #eee;}

@media (max-width: 599px){
    .container .movie_info img {display:block; float:none; margin:0 auto;}
    .container .movie_data {padding:0; padding-top:15px; text-align:center;}
    .container .movie_data .title {font-size:18px;}
    .container .movie_data .genres li {font-size:10px;}
    .container .movie_data .summary {font-size:12px;}
}

/* Movie Detail */
.container .detail {text-align:center;}
.container .detail img {float:none; width:230px;}
.container .detail .movie_data {margin-top:15px; padding:0;}
.container .detail .movie_data .year {display:block; font-weight:700; color:lightcoral;}

@media (max-width: 599px){
    .container .detail img {width:115px;}
}
 /* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* Font */

/* Common */
*,
*:before,
*:after {box-sizing:border-box;}
html,
body {height:100%; line-height:1.5; background:#eee; font-family:'Montserrat', sans-serif; font-size:14px; color:#999;}
h1,
h2,
h3,
h4,
h5,
h6 {line-height:1.2em; font-weight:700;}
a {color:inherit; text-decoration:none;}

/* Loader */
.loader {position:fixed; top:0; left:0; z-index:1; width:100%; height:100%;}
.loader .txt {position:absolute; top:50%; left:50%; width:30px; height:30px; margin-top:-15px; margin-left:-15px; border:5px solid rgba(0, 0, 0, .15); border-top-color:#666; border-radius:50%; text-indent:-9999px; animation:spin 1s linear infinite;}

@keyframes spin {
	to {transform: rotate(360deg);}
}

/* Header */
header {padding:30px 0 15px; text-align:center;}
header h1 {font-size:36px; color:firebrick;}

@media (max-width: 599px){
	header {padding:15px 0 0;}
    header h1 {font-size:24px;}
}
